@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
.heading{font-size:300px}h1{font-size:6rem}h1.is-light{color:white}

.navbar{width:100%;display:flex;flex-wrap:nowrap}.navbar__button{z-index:9999;border:none;background-color:white}.navbar__button.is-inverted{background-color:black}.navbar__icon{width:1.5em;height:1.5em;margin:2em}.menu{width:100%;display:flex;justify-content:space-between;padding:2em 4em}.menu__list{list-style-type:none;margin:0;padding:0;overflow:hidden}.menu__item{display:inline}.menu__item:first-child{margin-right:1em}.menu__item:first-child:after{content:"|";margin-left:1em}.menu__link{text-decoration:none;color:inherit}.sidemenu{position:absolute;display:flex;flex-flow:column wrap;align-content:space-between;left:0;background-color:white;height:100vh;padding:8em 3.5em 2em 3.5em;color:black;z-index:9998}.sidemenu__list{font-weight:700;list-style-type:none;margin:0;padding:0;overflow:hidden;font-size:4rem}.sidemenu__item:not(:last-child){margin-bottom:0.5em}.sidemenu__link,.sidemenu__bottom a{text-decoration:none;color:inherit}.sidemenu__link.active,.sidemenu__bottom a.active{text-decoration:line-through}.sidemenu__link:not(.active):hover:before{content:"+"}.sidemenu__bottom{margin-top:auto;display:flex;justify-content:space-between}.sidemenu__bottom :first-child{margin-right:6em}

.fullscreen-header{height:100vh;color:#fff;background-color:black;position:relative;z-index:1}.fullscreen-header .background{position:absolute;z-index:-1;right:0;height:100%;width:50%;background:url("https://images.unsplash.com/photo-1509384698014-34bc2fbdc0f0?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1542&q=80") no-repeat;background-size:cover;background-position:50% 60%;-webkit-filter:contrast(250%);filter:contrast(250%)}.fullscreen-header .background:before{content:"";position:absolute;left:0;width:50%;height:100%;background:linear-gradient(90deg, #000 0%, rgba(0,0,0,0) 100%)}.fullscreen-header .background:after{content:"";position:absolute;right:0;width:100%;height:100%;background:rgba(225,0,0,0.35)}.fullscreen-header .wrapper{width:50%;padding:6em 0;margin:auto;display:flex;flex-flow:row wrap}.fullscreen-header .wrapper .search{width:70%;padding:1em 0;margin-right:5%}.fullscreen-header .wrapper .search__button{width:25%;padding:1em 0;font-weight:700}.fullscreen-header .wrapper .search,.fullscreen-header .wrapper .search__button{border:0;color:white;background:transparent;border-bottom:2px solid white}.fullscreen-header .wrapper .location{width:100%;text-align:left;display:inherit}.fullscreen-header .wrapper .location :first-child{margin-right:1rem}

.grid-list{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:1em 2em}

.card{width:100%}.card__link{text-decoration:none;color:inherit}.card__avatar{background-repeat:no-repeat;background-size:cover;background-position:50% 50%;height:200px;-webkit-filter:grayscale(1) contrast(120%);filter:grayscale(1) contrast(120%);margin-bottom:0.5em}.card__details{display:flex;flex-flow:row nowrap;justify-content:space-between;align-items:center}

.button{font-weight:700;padding:1.5em 3em;color:black;background-color:transparent;border-bottom:1px solid black}.button--is-main{background-color:black;color:white;font-weight:700;padding:1.5em 3em}.is-first{margin-right:3em}

.lds-ellipsis{display:inline-block;position:absolute;width:80px;height:80px;top:calc(50% - 40px);left:calc(50% - 40px)}.lds-ellipsis div{position:absolute;top:32px;width:15px;height:15px;border-radius:50%;background:black;-webkit-animation-timing-function:cubic-bezier(0, 1, 1, 0);animation-timing-function:cubic-bezier(0, 1, 1, 0)}.lds-ellipsis div:nth-child(1){left:8px;-webkit-animation:lds-ellipsis1 0.6s infinite;animation:lds-ellipsis1 0.6s infinite}.lds-ellipsis div:nth-child(2){left:8px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(3){left:32px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(4){left:56px;-webkit-animation:lds-ellipsis3 0.6s infinite;animation:lds-ellipsis3 0.6s infinite}@-webkit-keyframes lds-ellipsis1{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes lds-ellipsis1{0%{transform:scale(0)}100%{transform:scale(1)}}@-webkit-keyframes lds-ellipsis3{0%{transform:scale(1)}100%{transform:scale(0)}}@keyframes lds-ellipsis3{0%{transform:scale(1)}100%{transform:scale(0)}}@-webkit-keyframes lds-ellipsis2{0%{transform:translate(0, 0)}100%{transform:translate(24px, 0)}}@keyframes lds-ellipsis2{0%{transform:translate(0, 0)}100%{transform:translate(24px, 0)}}

.artist{display:flex;flex-wrap:wrap;margin-top:4em}.artist__avatar{width:30vw;height:40vw}.artist__avatar--is-red{position:relative;width:30vw;height:40vw;margin:0 6em 4em 0;background-repeat:no-repeat;background-size:cover;background-position:50% 50%;-webkit-filter:saturate(140%);filter:saturate(140%)}.artist__avatar--is-red:after{content:"";position:absolute;width:30vw;height:40vw;background:rgba(225,0,0,0.55)}.details{width:calc(70vw - 11em)}.details__head{display:flex;justify-content:space-between}.details__rating{text-align:right;font-weight:700}.details__title{margin:0}.details__studio{text-align:right;align-self:flex-end}.details__studio h2{font-weight:700;font-size:2.2rem}.details__numbers{font-weight:700;margin:2em 0}.details__bio{margin:1em 0 3em 0}

.about-header{height:100vh;color:black;background-color:white;position:relative;z-index:1}.about-header .background-grey{position:absolute;z-index:-1;right:0;height:100%;width:50%;background:url("https://images.unsplash.com/photo-1507251217843-301ac7bf2afd?ixlib=rb-1.2.1&auto=format&fit=crop&w=1452&q=80") no-repeat;background-size:cover;background-position:50% 0%;-webkit-filter:grayscale(1);filter:grayscale(1)}.about{width:50%;height:calc(100% - 85px);display:flex;flex-flow:column wrap;padding:4em 15vw 0em 4em}.about__title{margin:0 0 30px 0;padding:0 0 30px 0;font-size:5rem;border-bottom:2px solid black}.about p:not(:last-child){margin-bottom:15px}.image-red{position:absolute;z-index:-1;left:calc(50% - 12vw);top:35%;height:24vw;width:24vw;background:url("https://images.unsplash.com/photo-1567581874653-5124a31cb23c?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1982&q=80") no-repeat;background-size:cover;background-position:50% 30%;-webkit-filter:contrast(250%);filter:contrast(250%)}.image-red:after{content:"";position:absolute;right:0;width:100%;height:100%;background:rgba(225,0,0,0.35)}

*,*::after,*::before{box-sizing:border-box;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html{font-size:62.5%}body{font-family:"Source Sans Pro", sans-serif;font-size:1.6rem;margin:0}p{margin:0.1em 0}h2{margin:0.1em 0}button,a{cursor:pointer;outline:0;border:0}input{outline:0}.container{margin:0 4em}.is-flex{display:flex}.is-center{align-items:center;justify-content:center}.is-bold{font-weight:700}

